*{
    font-family: "Roboto";
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.education{
    font-weight: bold;
}

.title{
    margin-top:15vh;
    text-align: center;
    font-size:50px;
    font-weight:bold;
    /* border-bottom: solid 3px black; */
}

.logo{
    margin-top: 12vh;
    margin-bottom: 4vh;
    width:23vw;
    height:17vw;
    
}

.college{
    display:flex;
    align-items: center;
    justify-items: center;
    flex-direction: column;
}

.flex{
    margin-top:10vh;
    display:flex;
    justify-content: space-around;
    margin-bottom:20vh;
}

.section{
    margin-top:2vh;
    margin-bottom:4vh;
    padding: 4vh 8vh 4vh 8vh;
    border-radius: 20%;
    filter: drop-shadow(0 0 0.75rem grey);
}

.college{
    text-align: center;
    font-weight: bolder;
}

.collegeName{
    font-size: 40px;
}

.gpa{
    margin-top:20px;
    font-size: 20px;
    font-weight: initial;
}

.degree{
    margin-top:20px;
    font-size: 28px;
    font-weight: initial;
}

.course{
    text-align:center;
    font-size: 20px;
    padding-bottom: 30px;
}

.section > ul > li{
    margin-top:10px;
    font-weight: lighter;
}